.articles {
  display: flex;
}

.article-container {
  margin-left: 1.2rem;
  background-color: hsl(0, 0%, 100%);
  border-bottom-left-radius: 0.5rem;
  border-bottom-right-radius: 0.5rem;
}
.latest-articles {
  background-color: hsl(0, 0%, 98%);
  margin: 0.3em;
  padding: 1.5em;
  padding-left: 3em;
}
.author {
  padding: 1em;
  margin-top: 6em;
  display: block;
  margin-bottom: 0;
  padding-bottom: 0;
}

.by {
  color: hsl(233, 8%, 62%);
}

.latest-articles h4 {
  color: hsl(233, 26%, 24%);
}

.latest-articles .articles img {
  border-top-left-radius: 0.5rem;
  border-top-right-radius: 0.5rem;
  min-width: 100%;
}

@media only screen and (max-width: 600px) {
  .articles {
    display: inline;
  }
  .article-container {
    margin-bottom: 2rem;
    padding: 0.5rem;
  }
  .latest-articles {
    padding-left: 0;
  }
  .author {
    margin-top: 0;
  }
  .latest-articles h2 {
    color: hsl(233, 26%, 24%);
    margin-bottom: 1rem;
    padding-left: 2rem;
  }
}
