.about h2 {
  color: hsl(233, 26%, 24%);
  margin-bottom: 1rem;
  padding-left: 2rem;
}

.about p {
  margin-top: 1rem;
  padding-left: 2rem;

  margin-bottom: 1rem;
  color: hsl(233, 8%, 62%);
}

.about-items {
  display: flex;
  padding-left: 2rem;
}
.about {
  padding-left: 2rem;
  margin-top: -5rem;
  text-align: left;
  background-color: hsl(220, 16%, 96%);
  padding-top: 5rem;
}

.about-list {
  padding: 0.5rem;
}

.about-list p {
  max-width: 70%;
  padding: 0;
}

@media only screen and (max-width: 600px) {
  .about {
    padding: 0;
    padding-top: 7rem;
    width: 100%;
  }
  .about p {
    padding: 0.3rem;
    text-align: center;
    margin: auto;
  }
  .about-items {
    flex-direction: column;
    padding: 0;
    text-align: center;
  }
  .about-list {
    margin: 0 auto;
  }
  .about-list p {
    margin: 0 auto;
  }
}
