.text {
  text-align: left;
  padding-left: 2rem;
  padding-top: 12rem;
  max-width: 40%;
}

.main-container {
  display: flex;
  left: 40vw;
  background-image: url(../images/bg-intro-desktop.svg);
  background-position: 34rem -1rem;
  height: 100%;
  background-repeat: no-repeat;
  padding-left: 3em;
}

p {
  margin-top: 1rem;
  margin-bottom: 1rem;
  color: hsl(233, 8%, 62%);
  font-size: 1.3em;
}

img {
  max-width: 100%;
  margin-bottom: -5rem;
}
@media only screen and (max-width: 800px) {
  .text {
    padding-top: 2rem;
  }
}

@media only screen and (max-width: 600px) {
  .main-container {
    /* display: block; */
    flex-direction: column-reverse;
    width: 100%;
    left: 0;
    background-position: -154px -389px;
    background-color: aliceblue;
    padding: 0;
  }

  img {
    margin-bottom: 0rem;
  }
  .text {
    text-align: center;
    margin-top: 40rem;
    padding: 0.8rem;
    max-width: 100%;
  }
  .container {
    background-image: url(../images/bg-intro-desktop.svg);
    height: 4vh;
  }
}

@media only screen and (max-width: 430px) {
  .text {
    margin-top: 30rem;
  }
}
