.footer {
  background-color: hsl(233, 26%, 24%);
  color: hsl(0, 0%, 100%);

  padding: 2rem 0;
  padding-left: 3em;
}
.flex-column {
  display: flex;
  justify-content: space-between;
  padding: 0;
  margin: 0;
}

.flex-column ul {
  list-style: none;
}

.flex-column ul li {
  padding: 0.3em;
  color: hsl(0, 0%, 100%);
}
.logo-footer {
  color: hsl(0, 0%, 100%);
  margin-top: 0;
  padding-left: 2rem;
}
.flex-column ul li a {
  color: hsl(0, 0%, 100%);
  text-decoration: none;
}

.bar {
  display: block;
  margin: 1rem auto;
}
.social-media {
  display: flex;
  justify-content: space-between;
  padding: 0;
  margin: 0;
}

.button {
  text-align: center;
  margin-right: 2rem;
}

.end {
  color: hsl(233, 8%, 62%);
}

@media only screen and (max-width: 600px) {
  .flex-column {
    flex-direction: column;
    text-align: center;
  }
  .flex-column ul li {
    margin: 0.5em;

    text-align: center;
  }

  .footer-nav-list {
    padding: 0;
    margin: 0 auto;
    text-align: center;
  }
  .footer-nav-list ul li {
    text-align: center;
    margin: 1em;
    text-align: center;
  }
  .footer {
    padding-left: 0;
  }
  .button {
    margin: 0 auto;
    margin-top: 1em;
    padding: 0.5rem;
  }
  .social-media {
    margin: 0 auto;
    display: flex;
  }
  .footer {
    margin: 0 auto;
    text-align: center;
  }
  .logo-footer {
    color: hsl(0, 0%, 100%);
    margin-top: 0;
  }
  .end {
    margin-top: 1.5rem;
  }
  .bar {
    margin-bottom: 1em;
  }
}
