.navbar-items {
  display: flex;
  padding-left: 3em;
}

.navbar-items ul {
  display: flex;
  max-width: 1400px;

  padding-right: 1rem;
  margin: auto auto;
  list-style: none;
}

.navbar-items ul li {
  padding: 1.3em;

  display: inline-block;
}

.navbar-items ul li a {
  text-decoration: none;
  cursor: pointer;
  font-family: "Public Sans", sans-serif;
  font-size: 1.5em;
  color: hsl(233, 8%, 62%);
}

.btn {
  margin: 25px 0;
  padding: 0.7rem 1.4rem;
  border-radius: 3rem;
  color: hsl(0, 0%, 100%);
  background-image: linear-gradient(
    to right,
    hsl(136, 65%, 51%),
    hsl(192, 70%, 51%)
  );
  border: none;
  outline: none;
  width: max-content;
  transition: box-shadow 450ms ease-in-out;
  cursor: pointer;
  z-index: -1;
}
.nav-links-icone {
  margin: auto;
}

.btn:hover {
  box-shadow: 0px 0px 5px hsl(233, 26%, 24%);
}
.nav-links {
  position: relative;
  text-decoration: none;
  font-weight: 400;
  color: blue;
  transition: all 0.35s ease;
}

.nav-links::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: hsl(136, 65%, 51%);
  visibility: hidden;
  -webkit-transform: scaleX(0);
  transform: scaleX(0);
  -webkit-transition: all 0.3s ease-in-out 0s;
  transition: all 0.3s ease-in-out 0s;
}

.nav-links:hover {
  color: hsl(192, 70%, 51%);
}

.nav-links:hover::before {
  visibility: visible;
  -webkit-transform: scaleX(1);
  transform: scaleX(1);
}

.btn-x {
  opacity: 0;
}
@media only screen and (max-width: 1000px) {
  .navbar-items {
    font-size: 0.8rem;
  }
}

@media only screen and (max-width: 600px) {
  .navbar-items ul {
    display: block;
    position: fixed;

    display: block;
    background-color: hsl(0, 0%, 100%);
    width: 100%;

    top: 80px;
    left: -200%;

    transition: all 1s;
  }
  .btn {
    display: none;
  }
  .btn-x {
    opacity: 1;
    margin-left: 3.5rem;
    position: absolute;
    right: 3rem;
    border: none;
    background-color: inherit;

    outline: none;

    padding: 1rem;
  }
  .nav-list {
    left: 0% !important;
  }
  .navbar-items ul li {
    display: block;
  }
  #check {
    display: none;
  }
}
